<style lang="scss">
.logo {
  outline: none;

  &__icon {
    width: rem(139);
    height: rem(25);

    path {
      fill: var(--color-black);
    }
  }

  @include mouse-device {
    padding: 1rem;
    transition: background var(--transition-duration);

    &__icon path {
      transition: fill var(--transition-duration);
    }

    &:hover,
    &:focus {
      background-color: var(--color-gothic);

      .logo__icon path {
        fill: var(--color-white);
      }
    }
  }
}
</style>
